    } 
}
.modal__input{
    margin-bottom: 24px;
    max-width: 534px;
    width: 100%;
    box-sizing: border-box;
    padding-left: 5px;
    height: 44px;
    border: 1.5px solid #E0E0E0;
}
.modal__input::placeholder{
   color: #C4C4C4;
}
.modal__btn{
    text-transform: uppercase;
    font-size: 16px;
    font-family: 'Montserrat';
    color: #232228;
    padding: 13px 24px;
    border: 1.5px solid @goldColor;
    background-color: #fff;
}

.modals__overlay{
    visibility: hidden;
    opacity: 0;
      
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 0 40px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(44, 41, 41, 0.686);
    z-index: 999999;
	transition: all 0.2s ease-in-out;

    .modal{
        display: none;
        box-sizing: border-box;
        max-width: 963px;
        width: 100%;
        padding: 24px;
        background-color: #fff;
    }
    
    .modal__inner{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        padding: 30px 18%;
        background-color: #fff;
        border: 1.5px solid #A4834E;
        position: relative;
        .close__modal{
            display: inline-block;
            position: absolute;
            bottom: 100%;
            left: 100%;
            height: 24px;
            width: 24px;
            transform: rotate(45deg);

            &::after,&::before{
                content: '';
                position: absolute;
            
                background-color: #A3A3A3;
                border-radius: 1px;
                
            }
            &::before{
                width: 24px;
                height: 1px;
                
                top: 11px;
                left: 0;
                // transform: rotate(90deg);

            }
            &::after{
                height: 24px;
                width: 1px;
                top: 0;
                left: 11px;
                // transform: rotate(40deg);
            }
        }
        .close__modal:hover{
            &::after,&::before{
                background-color: red;
            }
        }
    }   
}
.modals .modals__overlay--visible{
    visibility: visible;
    opacity: 1;
	transition: all 0.3s ease-in-out;
}
.modals .modal--visible{
display: block;
}
