}
.main__burger{
    display: none;
    padding: 15px 0 ;
    .burger__string{
        position: relative;
        width: 45px;
        height: 3px;
        border-radius: 3px;
        background-color: #fff;
        &::after,&::before{
            position: absolute;
            content: '';
            width: inherit;
            height: inherit;
            left: 0;
            border-radius: 3px;
            background-color: #fff;
            transition: all .2s linear;
        }
        &::after{
           top: -15px;
        }
        &::before{
            top: 15px;
         }
    }
}
.main__burger.swap{
    .burger__string{
        &::after{
            top: 0px;
            transform: rotate(-45deg);

        }
        &::before{
            top: 0px;
           
            transform: rotate(45deg);
         }
    }
}
