.slider_wrapper{
    display: inline-block;
    width: 70% ;
    position: relative;
    &::after{
        z-index: 10000;
        content: url(../images/closeQu.png);
        position: absolute;
        right: calc(var(--offset)*10);
        top: calc(calc(var(--offset)*15  - 25px))
    }
    &::before{
        content: url(../images/openQu.png);
        position: absolute;
        left: calc(var(--offset)*10);
        bottom: -20px;
        z-index: 10000;

    }
}
.keen-slider{
