}
.keen-slider__slide{
    box-sizing: border-box;
    overflow: visible !important;
    padding: 90px;
    background-color: #ffffff;
    .slider__text{
        font-size: 16px;
        font-style: italic;
        margin-bottom: calc(var(--offset)*10);
    }
    .slider__title{
        font-size: 16px;
