        font-weight: 800;
        margin-bottom: 16px;
    }
    .slider__subTitle{
        color: @greyColor;
        font-size: 14px;
    }
    .slider__img{
        display: inline-block;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%,-50%);
        border-radius: 50%;
        padding: 10px;
        background-color:#F6F4F5;
        z-index: 10;
        .slide__img{
            vertical-align: middle;
        }
    }
}
.slider__border{
    position: absolute;
    top: calc(60px + 70px);
    bottom: 70px;
    width: 21px;
    background-color: transparent;
    border:1.5px solid #A98A58;
}

.slideRight{
    left: 100%;
    border-left: transparent;
}
.slideLeft{
    right: 100%;
    border-right: transparent;

}

