                color: @goldColor;
            }
        }
        .maps__consultation{
            display:block;
            margin: 20px 0;
            font-size: 16px;
            font-family: 'Montserrat';
            text-transform: uppercase;

        }
        .maps__input{
            display: flex;
        }
        .maps__tel{
            box-sizing: border-box;
            max-width: 300px;
            width: 100%;
            height: 44px;
            display: block;
            padding: 0 0 0 10px;
        }
        .maps__telSup{ 
            display: inline-block;
            font-size: 16px;
            text-transform: uppercase;
            color: #fff;
            margin-left: 8px;
            height: 44px;
            width: 170px;
            padding: 0 18px;
            line-height:  44px;
            font-family: 'Montserrat';
            border: 1.5px solid @goldColor;
        }
    }
}

.footer{
    background-color: #F6F4F5;
    text-align: center;
    
    .footer__text{
        display: inline-block;
         font-size: 12px;
         color: #5D5D5D;
         margin: 22px 0;
    }
}

@media screen and (max-width:1200px) {
    .decision{
        width: 100%;
    }
    .monument{
        display: none;
    }
    .chooseUs__item{
        width: calc(50% - (calc(var(--offset)*3)*2));
        text-align: center;
    }
    .workers__inner{
        flex-wrap: wrap;
    }
    .workers__content{
    width: calc(100% - calc(var(--offset) * 3 * 2));
    }
    .workers__testa{
    width: calc(100% - calc(var(--offset) * 3 * 2));
    }
    .workers__test{
        justify-content: center
    }
    .workers__item{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .trust__item{
        width: calc(33.3333% -  (calc(var(--offset)*3)*2)) ;
    }
    .know__item{
        width: calc(50% - (calc(var(--offset) * 3 * 2)));
        text-align: center;
        &:nth-child(1){
            background: none;
        }
        &:nth-child(1) .know__item__img{
            opacity: 10;
        }
        &:nth-child(1) .know__item__content .know__item__btn{
            color: #232228;
        }
        &:nth-child(1) .know__item__content .know__item__title{
            color: #232228;
        }
    }
    .maps__inner{
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        &>*{
            width:calc(100%);
        }
    }
    .goldenWall .moveLeft{
        transform: none;
    }
    .goldenWall .boss{
        right: auto;
        left: 0;
    }
    .chief .container {
        padding: 0;
    }
    .chief__inner{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0;
    }
    // .goldenWall .boss{
    //     right:unset;
    //     left: 0;
    // }
}

@media screen and (max-width:999px) {
    .chooseUs__item .chooseUs__item__text{
        padding-right: 0;
    }
    .chief__inner{
        padding: 25px;
        // &::after{
        //     content: none;
        // }
        .chief__inner__text{
            width: 100%;
            &::after,&::before{
                content: '';
            }
        }
        .chief__inner__footer{
            width: 100%;
        }
        .chief__inner__footer{
            margin-top: 0;
        }
    }
    .adds .adds__form{
        display: block;
    }
    .adds .adds__form .adds__text{
        margin-bottom: 24px;
    }
    .modals__overlay .modal__inner{
        padding: 20px 20px;
    }
}
@media screen and (max-width:900px) {
   
    .header{
        position: relative;
    }
    .header__inner{
        padding: 10px 0 ;

        .nav{
            display: block;
           
            .nav__inner {
                position: absolute;
                top: 100%;
                width: 100%;
                left: 0;
                visibility: hidden;
                opacity: 0;
                background-color: #0f0f0f;
                z-index: 1000;
                flex-direction: column;
                margin: 0;
	            transition: all 0.3s ease-in-out;

            }
            .nav__inner.hide{
                visibility: visible;
                opacity: 1;
	            transition: all 0.3s ease-in-out;
            }
            .nav__link{
                margin: 0;
                padding: 16px;
                
                &::after{
                   content: none;
                }
                border-bottom: 1.5px solid @goldColor;
            }
        }
        .main__burger{
            display: inline-block;
        }
    }
    .chief__inner{
        &::after{
            content: none;
        }
    }
    .stats{
        width: 100%;
    } 
    .boss{
        display: none;
    }
    .goldenWall{
        all: initial;
        width: calc(100% / 12 * 3);
        background: url(../images/goldenWall.png) no-repeat center;
        background-size: cover;
        
        .moveLeft{
            display: none;
        }
    }
}

