:root{
    --offsetText: 8px;
    --offset: 4px;
}
@offsetContent: 24px;
@goldColor:#E3C37A;
@greyColor:#908F90;
body{
color: #FFFFFF;
}
.h1{
    color: #FFFFFF;
    font-size: calc(var(--offset)*7);
    text-transform: uppercase;
}
.container{
    display: block;
    max-width: 1290px;
    padding: calc(var(--offset)*6);;
    margin: 0 auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
    margin: 0  calc( var(--offset) * -3);
}

.col{
    margin: calc(var(--offset)*3);
    box-sizing: border-box;
}
