img{
    max-width: 100%;
    height: auto;
}
h1,h2,h3,h4,h5,h6,p,ul,li{
	display: block;
	margin: 0;
	font-size: 100%;
	font-weight: normal;
}

ul,ol{
	list-style: none;
	padding: 0;
}

strong{
	font-weight: normal;
}

em{
	font-style: normal;
}

input{
	outline: none;
}

input:focus{
	outline: none;
}