.modal__title {
  font-size: calc(var(--offsetText) * 5);
  text-transform: uppercase;
  font-family: 'Montserrat';
  color: #232228;
}
@media screen and (max-width: 888px) {
  .modal__title {
    font-size: 30px;
  }
}
@media screen and (max-width: 600px) {
  .modal__title {
    font-size: 25px;
  }
}
.modal__gold {
  font-size: calc(var(--offsetText) * 5);
  text-transform: uppercase;
  font-family: 'Montserrat';
  color: #A4834E;
  margin-bottom: 40px;
}
@media screen and (max-width: 888px) {
  .modal__gold {
    font-size: 30px;
  }
}
@media screen and (max-width: 600px) {
  .modal__gold {
    font-size: 25px;
  }
}
.modal__input {
  margin-bottom: 24px;
  max-width: 534px;
  width: 100%;
  box-sizing: border-box;
  padding-left: 5px;
  height: 44px;
  border: 1.5px solid #E0E0E0;
}
.modal__input::placeholder {
  color: #C4C4C4;
}
.modal__btn {
  text-transform: uppercase;
  font-size: 16px;
  font-family: 'Montserrat';
  color: #232228;
  padding: 13px 24px;
  border: 1.5px solid #E3C37A;
  background-color: #fff;
}
.modals__overlay {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 0 40px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(44, 41, 41, 0.686);
  z-index: 999999;
  transition: all 0.2s ease-in-out;
}
.modals__overlay .modal {
  display: none;
  box-sizing: border-box;
  max-width: 963px;
  width: 100%;
  padding: 24px;
  background-color: #fff;
}
.modals__overlay .modal__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 30px 18%;
  background-color: #fff;
  border: 1.5px solid #A4834E;
  position: relative;
}
.modals__overlay .modal__inner .close__modal {
  display: inline-block;
  position: absolute;
  bottom: 100%;
  left: 100%;
  height: 24px;
  width: 24px;
  transform: rotate(45deg);
}
.modals__overlay .modal__inner .close__modal::after,
.modals__overlay .modal__inner .close__modal::before {
  content: '';
  position: absolute;
  background-color: #A3A3A3;
  border-radius: 1px;
}
.modals__overlay .modal__inner .close__modal::before {
  width: 24px;
  height: 1px;
  top: 11px;
  left: 0;
}
.modals__overlay .modal__inner .close__modal::after {
  height: 24px;
  width: 1px;
  top: 0;
  left: 11px;
}
.modals__overlay .modal__inner .close__modal:hover::after,
.modals__overlay .modal__inner .close__modal:hover::before {
  background-color: red;
}
.modals .modals__overlay--visible {
  visibility: visible;
  opacity: 1;
  transition: all 0.3s ease-in-out;
}
.modals .modal--visible {
  display: block;
}
.header {
  border-bottom: 1.5px solid #3E3C3F;
}
.header .container {
  padding: 0 calc(var(--offset)*6);
}
.header .logo {
  font-size: 18.3px;
}
.main__content {
  position: relative;
}
.header__inner {
  justify-content: space-between;
  align-items: center;
}
.main__phone {
  color: #E3C37A;
  font-size: 16px;
}
.main__burger {
  display: none;
  padding: 15px 0 ;
}
.main__burger .burger__string {
  position: relative;
  width: 45px;
  height: 3px;
  border-radius: 3px;
  background-color: #fff;
}
.main__burger .burger__string::after,
.main__burger .burger__string::before {
  position: absolute;
  content: '';
  width: inherit;
  height: inherit;
  left: 0;
  border-radius: 3px;
  background-color: #fff;
  transition: all 0.2s linear;
}
.main__burger .burger__string::after {
  top: -15px;
}
.main__burger .burger__string::before {
  top: 15px;
}
.main__burger.swap .burger__string::after {
  top: 0px;
  transform: rotate(-45deg);
}
.main__burger.swap .burger__string::before {
  top: 0px;
  transform: rotate(45deg);
}
